@import url('https://fonts.googleapis.com/css2?family=Karla:wght@700&family=Roboto:wght@400;500;700&display=swap');

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

#root {
	position: relative;
}

.container {
	width: 1020px;
	height: 100%;

	margin: 0 auto;

	position: relative;
}

.page-enter {
	opacity: 0;
	transform: scale(.9);
}
 
.page-enter-active {
	opacity: 1;
	transform: scale(1);
	transition: all 200ms;
}
 
.page-exit {
	opacity: 1;
	transform: scale(1);
}
 
.page-exit-active {
	opacity: 0;
	transform: scale(0.9);
	transition: all 200ms;
}

@media (max-width: 1200px) {
	.container {
		max-width: 960px;
	}
}

@media (max-width: 992px) {
	.container {
		max-width: 720px;
	}
}

@media (max-width: 768px) {
	.container {
		max-width: 540px;
	}
}

@media (max-width: 576px) {
	.container {
		max-width: 90%;
	}
}